<template>
  <div
    class="accordion"
  >
    <div
      :ref="section"
      class="accordion__item"
      :class="{'is-active': activeItems.includes(`${ section }`)}"
    >
      <div
        :class="`accordion__title ${titleClass}`"
        :aria-controls="control"
        :aria-expanded="activeItems.includes(`${ section }`)"
        @click.prevent="() => toggleItem(`${ section }`)"
      >
        <slot name="header" />
      </div>

      <accordion-item
        class="navlist"
        :is-active="activeItems.includes(`${ section }`)"
        :index="1"
      >
        <slot
          name="item"
          :item="item"
        />
      </accordion-item>
    </div>
  </div>
</template>

<script>

/**
 * Vue: Bundle accordion (bundle-accordion)
 * -----------------------------------------------------------------------------
 * Bundle accordion.
 *
 */
import AccordionItem from '~global/accordion/accordion-item'
import breakpoints from '~/config/breakpoints'

export default {
  name: 'BundleAccordion',

  components: {
    AccordionItem,
  },

  props: {
    control: {
      type: String,
      default: 'content-1',
    },
    section: {
      type: String,
      default: '',
    },
    titleClass: {
      type: String,
      default: 'h5',
    },
  },

  data() {
    return {
      global: true,
      settings: {
        singleOpen: true,
        expandInitial: false,
        expandAll: false,
        animate: true,
      },
      activeItems: [],
    }
  },

  methods: {

    /**
     * Toggle targeted accordion item.
     * @param {HTMLElement} control - The clicked accordion control.
     */
    toggleItem(control) {
      const item = this.$refs[control]
      if (this.isItemExpanded(control)) {
        const { activeItems } = this
        const itemIndex = activeItems.indexOf(control)
        activeItems.splice(itemIndex, 1)

      } else {
        this.activeItems.push(control)
      }

      if (this.settings.scrollTo) {
        this.setWindowScroll(item)
      }
    },

    /**
     * Check if item is active.
     * @param {HTMLElement} control - The accordion item.
     * @returns {Boolean}
     */
    isItemExpanded(control) {
      return this.activeItems.includes(control)
    },

   /**
   * Set window scroll
   * @param {Object} target - The returned element that was clicked
   */
    setWindowScroll(target) {
      if (!window.matchMedia(`(min-width: ${breakpoints.l})`).matches) {
          target.item.scrollIntoView({
            behavior: 'smooth',
          })
      }
    },
  },
}
</script>

<style lang="scss">
@import './bundle-accordion';
</style>
